import React, {Dispatch, SetStateAction, useCallback, useEffect, useState} from "react";
import {Select} from "antd";
import "antd/es/select/style/index.css";
import {Building, RichFamily} from "../../api/types";
import {getFamilies} from "../../api";
import {BuildingLabels} from "../../lib/label";
const {Option} = Select;

export function emptyBuildings(): Building[] {
    return [
        {building: 1, families: []},
        {building: 2, families: []},
        {building: 3, families: []},
    ];
}

export async function getRichFamilies(): Promise<Building[]> {
    const families = await getFamilies();
    const buildings = emptyBuildings();
    families.forEach((family) => {
        const [building, room] = family.id.split("-");
        const rich: RichFamily = {
            ...family,
            room,
            building: parseInt(building),
        };
        const theBuilding = buildings.find(b => b.building === parseInt(building));
        if (theBuilding) {
            theBuilding.families.push(rich);
        }
    });
    return buildings;
}


export function useFamilySelect(initRoomId?: string):
    [Building[], string | undefined, Dispatch<SetStateAction<string | undefined>>] {
    const [buildings, setBuildings] = useState<Building[]>(emptyBuildings());
    const [roomId, setRoomId] = useState<string | undefined>(initRoomId);

    useEffect(() => {
        getRichFamilies().then(buildings => {
            setBuildings(buildings);
            setRoomId(current => {
                if (current === undefined) {
                    const firstFamily = buildings[0].families[0];
                    if (firstFamily) {
                        return firstFamily.id;
                    }
                } else {
                    return current;
                }
            });
        });
    }, []);

    return [buildings, roomId, setRoomId];
}

export interface FamilySelectProps {
    buildings: Building[];
    roomId?: string;
    setRoomId: (id?: string) => void;
}

const FamilySelect: React.FC<FamilySelectProps> = props => {
    const {buildings, roomId, setRoomId} = props;
    let currentBuilding: Building;
    if (roomId) {
        const b = buildings.find(building => building.families.some(f => f.id === roomId));
        currentBuilding = b || buildings[0];
    } else {
        currentBuilding = buildings[0];
    }
    
    const setBuilding = useCallback((bid: number) => {
        if (currentBuilding.building !== bid) {
            const newBuilding = buildings.find(b => b.building === bid);
            if (newBuilding) {
                const f = newBuilding.families[0];
                setRoomId(f ? f.id : undefined);
            }
        }
    }, [buildings, currentBuilding.building, setRoomId]);
    
    const setRoom = useCallback((id: string) => {
        setRoomId(id);
    }, [setRoomId]);

    return (
        <div className="c-family-select">
            <div className="label">楼栋号：</div>
            <Select value={currentBuilding.building} onChange={setBuilding} dropdownClassName="c-family-select-dropdown">
                {
                    buildings.map(building => {
                        return (
                            <Option key={building.building} value={building.building}>
                                {BuildingLabels[building.building]}
                            </Option>
                        )
                    })
                }
            </Select>
            <div className="label">门牌号：</div>
            <Select value={roomId} onChange={setRoom} dropdownClassName="c-family-select-dropdown">
                {
                    currentBuilding.families.map(family => {
                        return (
                            <Option key={family.id} value={family.id}>
                                {family.room}
                            </Option>
                        )
                    })
                }
            </Select>
        </div>
    );
};

export default FamilySelect;