import React, {useCallback, useState} from "react";
import {RichFamily} from "../../api/types";
import moment from "moment";
import {DeviceStatusLabels} from "../../lib/label";
import { useHistory } from "react-router-dom";
import {PATH_FAMILY} from "../../lib/routes";

interface OverlayProps {
    onMouseEnter: () => void;
    onMouseLeave: () => void;
    onClick: () => void;
}

const LeftOverlay: React.FC<OverlayProps> = props => {
    return (
        <svg width="100%" height="100%" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 178 103">
            <path
                className="family-overlay"
                d="M0.38,32.447l120.065,69.553l56.925,-31.17v-32.862l-56.926,31.482l-120.063,-68.95z"
                fillOpacity="0"
                fill="#000000"
                {...props}
            />
        </svg>
    )
};

const RightOverlay: React.FC<OverlayProps> = props => {
    return (
        <svg width="100%" height="100%" viewBox="0 0 117 79" xmlns="http://www.w3.org/2000/svg">
            <path
                className="family-overlay"
                fill="#000000"
                fillOpacity="0"
                d="M116.224,0v33.503l-55.381,32.144l-17.6,-9.42l-16.63,9.42l-3.909,-1.933l-22.704,13.992v-33.651l22.704,-12.831l3.91,1.851l16.63,-9.572l17.599,9.572z"
                {...props}
            />
        </svg>
    )
};



export interface FamilyProps {
    data: RichFamily;
    interactive: boolean;
    index: number;
}

const FamilyView: React.FC<FamilyProps> = props => {
    const history = useHistory();
    const {data, interactive, index} = props;
    const [hover, setHover] = useState(false);
    const click = useCallback(() => {
        if (interactive) {
            history.push(`${PATH_FAMILY}?id=${data.id}`);
        }
    }, [data.id, history, interactive]);

    let side: string, Overlay: React.FC<OverlayProps>;
    if (index % 2 === 0) {
        side = "left";
        Overlay = LeftOverlay;
    } else {
        side = "right";
        Overlay = RightOverlay;
    }
    const level = Math.floor(index / 2);
    const hasAlarm = data.alarm;

    return (
        <div className={`family ${side} level-${level} ${interactive && hover ? "hover" : ""} ${hasAlarm ? "alarm" : ""}`}>
            <Overlay
                onMouseEnter={() => setHover(true)}
                onMouseLeave={() => setHover(false)}
                onClick={click}
            />
            {hasAlarm && (
                <div className="alarm-content">
                    检测到
                    <em>{data.id}</em>
                    未授权设备于
                    <em>{moment(data.eventTime).format("YYYY-MM-DD HH:mm:ss")}</em>
                    {DeviceStatusLabels[data.eventType]}
                    <br/>
                    平均功率
                    <em>{data.power}W</em>
                </div>
            )}
        </div>
    );
};

export default FamilyView;