import React from "react";
import {DeviceType} from "../../api/types";
import ReactEcharts from "echarts-for-react";
import {powerHourly} from "./charts";
import {DeviceLabels} from "../../lib/label";

export interface ChartGroupProps {
    data: {
        type: DeviceType,
        values: [number, number][]
    }[]
}

const colors = ["#FF1A3B", "#C95FF2", "#FF7F00", "#00ADFF", "#61E493", "#9382FF", "#F8E71C"];

const ChartGroup: React.FC<ChartGroupProps> = props => {
    const {data} = props;

    return (
        <div className="c-chart-group">
            {data.map((d, i) => (
                <div key={d.type} className="item">
                    <ReactEcharts
                        notMerge
                        className="chart"
                        option={powerHourly(d.values, colors[i])}
                        style={{width: "2.4rem", height: "1.3rem"}}
                    />
                    <div className="name">{DeviceLabels[d.type]}</div>
                </div>
            ))}
        </div>
    );
};

export default ChartGroup;