export enum AlarmType {
    NORMAL,
    EXCEPTION,
}

export enum DeviceStatusType {
    CLOSED,
    OPEN,
}

export enum DeviceType {
    ALL,
    FRIDGE,
    WASHER,
    AIR_CONDITIONER,
    RICE_COOKER,
    MICROWAVE,
    ELECTRIC_KETTLE,
    OTHER
}

export enum DeviceAuthType {
    AUTH,
    UN_AUTH
}

export interface Indexed<T> {
    [k: string]: T;
}

export interface PageAlarmInfo {
    totalPage: number;
    allPowerInfos: AlarmInfo[];
}

export interface AlarmInfo {
    alarmType: AlarmType;
    deviceStatus: DeviceStatusType;
    time: number;
    deviceType: DeviceType;
    power: number;
}

export interface AllPowerInfo {
    totalPower: number;
    totalDayEPower: number;
    totalMonthEPower: number;
    powerMap: Indexed<number>;
    dayEPower: Indexed<number>;
    monthEPower: Indexed<number>;
}

export interface Family {
    id: string;
    info: string;
    eventType: DeviceStatusType;
    eventTime: number;
    power: number;
    alarm: boolean;
}

export interface RichFamily extends Family {
    building: number;
    room: string;
}

export interface Building {
    building: number;
    families: RichFamily[];
}

export interface PowerInfo {
    time: number;
    power: number;
}

export interface DevicePowerInfo {
    [k: string]: PowerInfo[];
}

export interface EPInfo {
    time: number;
    epower: number;
}

export interface DeviceEPInfo {
    [k: string]: EPInfo[];
}

export interface DeviceLoad {
    [k: string]: number;
}
