import {fetch} from "../lib/fetch";
import {AllPowerInfo, DeviceEPInfo, DeviceLoad, DevicePowerInfo, DeviceType, Family} from "./types";

export const getFamilies = async (): Promise<Family[]> =>
    await fetch("/main/all_family");

export const getAllPowerInfo = async (): Promise<AllPowerInfo> =>
    await fetch("/main/all_power_info");

export const getAllDevicePowerInfo = async (start: number, end: number, types: DeviceType[]): Promise<DevicePowerInfo> =>
    await fetch("/main/all_device_power_info", {
        query: { start, end, types }
    });

export const getAllDeviceEPInfo = async (start: number, end: number, types: DeviceType[]): Promise<DeviceEPInfo> =>
    await fetch("/main/all_device_e_power_info", {
        query: { start, end, types }
    });

export const getDeviceLoad = async (): Promise<DeviceLoad> =>
    await fetch("/main/device_load");