import {Context, createContext} from "react";

export interface MutableContextValue<T> {
    value: T;
    setValue: (v: T) => void;
}

const NOOP = () => {};

export function crateMutableContext<T>(
    defaultValue: T,
    defaultSetValue?: (v: T) => void
) {
    return createContext<MutableContextValue<T>>({
        value: defaultValue,
        setValue: defaultSetValue || NOOP,
    });
}

export const RootContainerContext: Context<() => HTMLElement> = createContext(() => document.body);
