import React, { CSSProperties } from "react";
import lottie, { AnimationItem } from "lottie-web";

export interface LottieProps {
  style?: CSSProperties;
  className?: string;
  data: any;
  renderer: "svg" | "canvas" | "html";
  loop: boolean | number;
  play: boolean;
  speed: number;
  reverse: boolean;
}

export default class Lottie extends React.Component<LottieProps> {
  static defaultProps: Pick<
    LottieProps,
    "renderer" | "loop" | "play" | "speed" | "reverse"
  > = {
    renderer: "svg",
    loop: true,
    play: true,
    speed: 1,
    reverse: false
  };

  containerRef = React.createRef<HTMLDivElement>();
  private animation?: AnimationItem;

  componentDidMount(): void {
    this.initAnimation();
  }

  componentDidUpdate(
    prevProps: Readonly<LottieProps>,
    prevState: Readonly<{}>
  ): void {
    if (this.props.data !== prevProps.data) {
      this.animation && this.animation.destroy();
      this.initAnimation();
    } else {
      if (prevProps.play !== this.props.play) {
        if (this.props.play) {
          this.animation && this.animation.play();
        } else {
          this.animation && this.animation.pause();
        }
      }
      if (prevProps.speed !== this.props.speed) {
        this.animation && this.animation.setSpeed(this.props.speed);
      }
      if (prevProps.reverse !== this.props.reverse) {
        this.animation &&
          this.animation.setDirection(this.props.reverse ? -1 : 1);
      }
    }
  }

  initAnimation(): void {
    const { data, renderer, loop, play, speed, reverse } = this.props;
    this.animation = lottie.loadAnimation({
      container: this.containerRef.current as HTMLDivElement,
      animationData: data,
      renderer,
      loop,
      autoplay: play
    });
    this.animation.setSpeed(speed);
    if (reverse) {
      this.animation.setDirection(-1);
    }
  }

  render() {
    return (
      <div
        ref={this.containerRef}
        style={this.props.style}
        className={this.props.className}
      />
    );
  }
}
