import React from "react";
import i_daily from "../assets/i_daily.png";
import i_monthly from "../assets/i_monthly.png";
import i_power from "../assets/i_power.png";
import i_ac from "../assets/i_ac.png";
import i_fridge from "../assets/i_fridge.png";
import i_kettle from "../assets/i_kettle.png";
import i_microwave from "../assets/i_microwave.png";
import i_rice_cooker from "../assets/i_rice_cooker.png";
import i_washer from "../assets/i_washer.png";
import i_all from "../assets/i_all.png";
import i_other from "../assets/i_other.png";
import {BaseProps} from "./common";
import {DeviceType} from "../api/types";

export interface RemImageProps extends BaseProps {
    width: number;
    height: number;
    src: string;
    alt?: string;
    onClick?: () => void;
}

export const RemImage: React.FC<RemImageProps> = props => {
    const {src, alt, width, height, className, style, onClick} = props;
    return (
        <img
            className={className}
            src={src}
            alt={`${alt || ""}`}
            style={{
                display: "block",
                width: `${width / 100}rem`,
                height: `${height / 100}rem`,
                ...style
            }}
            onClick={onClick}
        />
    );
};

function image(src: string, width: number, height: number): React.FC<BaseProps> {
    return props =>
        <RemImage
            width={width} height={height} src={src}
            {...props}
        />
}

export const IconPower = image(i_power, 48, 64);
export const IconDaily = image(i_daily, 51, 51);
export const IconMonthly = image(i_monthly, 51, 51);

export const IconAll = image(i_all, 68, 68);
const IconFridge = image(i_fridge, 68, 68);
const IconWasher = image(i_washer, 68, 68);
const IconAc = image(i_ac, 68, 68);
const IconRiceCooker = image(i_rice_cooker, 68, 68);
const IconMicrowave = image(i_microwave, 68, 68);
const IconKettle = image(i_kettle, 68, 68);
const IconOther = image(i_other, 68, 68);

interface IDeviceIcons {
    [k: string]: React.FC<BaseProps>;
}

export const DeviceIcons: IDeviceIcons = {
    [DeviceType.FRIDGE]: IconFridge,
    [DeviceType.WASHER]: IconWasher,
    [DeviceType.AIR_CONDITIONER]: IconAc,
    [DeviceType.RICE_COOKER]: IconRiceCooker,
    [DeviceType.MICROWAVE]: IconMicrowave,
    [DeviceType.ELECTRIC_KETTLE]: IconKettle,
    [DeviceType.OTHER]: IconOther,
};
