import React from "react";
import {DeviceModels} from "./DeviceModels";
import {AlarmInfo, DeviceStatusType, DeviceType} from "../../api/types";
import {roundDecimal} from "../../lib/util";

interface DeviceInfo {
    type: DeviceType;
    left: number;
    top: number;
}

export const displayDevices: DeviceInfo[] = [
    {type: DeviceType.FRIDGE, left: 1260, top: 315},
    {type: DeviceType.WASHER, left: 560, top: 530},
    {type: DeviceType.AIR_CONDITIONER, left: 730, top: 430},
    {type: DeviceType.RICE_COOKER, left: 1170, top: 270},
    {type: DeviceType.MICROWAVE, left: 1110, top: 210},
    {type: DeviceType.ELECTRIC_KETTLE, left: 1100, top: 390},
];


export interface RoomViewProps {
    data: Map<DeviceType, AlarmInfo>;
}

const RoomView: React.FC<RoomViewProps> = props => {
    const {data} = props;

    return (
        <div className="c-room-view">
            {displayDevices.map((device) => {
                const Model = DeviceModels[device.type];
                const info = data.get(device.type);
                return (
                    <Model
                        key={device.type}
                        left={device.left}
                        top={device.top}
                        type={device.type}
                        power={info ? roundDecimal(info.power / 1000, 2) : 0}
                        status={info ? info.deviceStatus : DeviceStatusType.CLOSED}
                        time={info && info.time}
                    />
                );
            })}
        </div>
    );
};

export default RoomView;