import React from "react";
import {RichFamily} from "../../api/types";
import FamilyView from "./FamilyView";

export interface BuildingProps {
    name: string;
    families: RichFamily[];
    current: boolean;
}

const Building: React.FC<BuildingProps> = props => {
    const {name, families, current} = props;
    return (
        <div className={`c-building ${current ? "current" : ""}`}>
            <div className="name">{name}</div>
            <div className="families">
                {families.map((family, i) => (
                    <FamilyView key={i} data={family} interactive={current} index={i}/>
                ))}
            </div>
        </div>
    );
};

export default Building;