import React, {ReactNode, useCallback, useContext, useState} from "react";
import {crateMutableContext} from "../../lib/context";
import "./index.scss";
import {BaseProps} from "../common";

const DrawerContext = crateMutableContext<string | undefined>(undefined);

export interface OverviewItemProps {
    id: string;
    icon: ReactNode;
    title: string;
    value: string | number;
    unit: string;
    graph: ReactNode;
}

export const OverviewItem: React.FC<OverviewItemProps> = props => {
    const { id, icon, title, value, unit, graph } = props;
    const { value: activeDrawer, setValue: setActiveDrawer} = useContext(DrawerContext);
    const active = id === activeDrawer;
    const toggleDrawer = useCallback(() => {
        setActiveDrawer(active ? undefined : id);
    }, [active, id, setActiveDrawer]);
    
    return (
        <div className="item">
            <div className="icon">{icon}</div>
            <div className="brief">
                <div className="title">{title}</div>
                <div className="value">
                    <em>{value}</em>
                    <u>{unit}</u>
                </div>
            </div>
            <div className="draw-button"
                 onClick={toggleDrawer}
            />
            <div hidden={!active} className={`drawer ${active ? "active" : ""}`}>
                <DrawerPanel title={title} doClose={toggleDrawer}>
                    {graph}
                </DrawerPanel>
            </div>
        </div>
    )
};

export const OverviewWithDrawerGraph: React.FC<BaseProps> = props => {
    const {className, style} = props;
    const [activeDrawer, setActiveDrawer] = useState<string>();

    return (
        <DrawerContext.Provider value={{
            value: activeDrawer,
            setValue: setActiveDrawer
        }}>
            <div
                className={"c-overview-with-drawer-graph " + (className || "")}
                style={style}
            >
                {props.children}
            </div>
        </DrawerContext.Provider>
    );
};

export interface DrawerPanelProps {
    title: string;
    doClose: () => void;
}

export const DrawerPanel: React.FC<DrawerPanelProps> = props => {
    const {title, children, doClose} = props;

    return (
        <div className="drawer-panel">
            <div className="title">{title}</div>
            <i className="close" onClick={doClose}/>
            <div className="content">{children}</div>
        </div>
    );
};