import {useCallback, useEffect, useRef, useState} from "react";
import {Observable} from "rxjs";
import { useLocation } from "react-router-dom";

export function useRefresh(): () => void {
    const [, setSeq] = useState(0);
    return useCallback(() => setSeq(i => i + 1), []);
}

export function useAsync<T>(action: () => Promise<T>): T | undefined;
export function useAsync<T>(action: () => Promise<T>, defaultValue: T): T;
export function useAsync<T>(action: () => Promise<T>, defaultValue?: T): T | undefined {
    const [value, setValue] = useState<T>(defaultValue as T);
    useEffect(() => {
        action().then(val => setValue(val));
    }, [action]);
    return value;
}

export function useAsyncCycle<T>(action: () => Promise<T>, interval: number): T | undefined;
export function useAsyncCycle<T>(action: () => Promise<T>, interval: number, defaultValue: T): T;
export function useAsyncCycle<T>(action: () => Promise<T>, interval: number, defaultValue?: T): T | undefined {
    const intervalRef = useRef(interval);
    const [value, setValue] = useState<T>(defaultValue as T);
    const [cycles, setCycles] = useState(0);
    useEffect(() => {
        action().then(val => setValue(val));
    }, [action, cycles]);

    useEffect(() => {
        const handle = setInterval(() => setCycles(i => i + 1), intervalRef.current);
        return () => clearInterval(handle);
    }, []);

    return value;
}

export function useObservable<D>(observable: Observable<D>): D | undefined;
export function useObservable<D>(observable: Observable<D>, init: D): D;
export function useObservable<D>(
    observable: Observable<D>,
    init?: D
): D | undefined {
    const [data, setData] = useState<D>(init as D);
    useEffect(() => {
        const subscription = observable.subscribe(setData);
        return () => {
            subscription.unsubscribe();
        };
    }, [observable, setData]);

    return data;
}

export function useQuery() {
    return new URLSearchParams(useLocation().search);
}
