import React, {useState} from "react";
import {RichFamily} from "../../api/types";
import {CSSTransition, TransitionGroup} from "react-transition-group";
import Building from "./Building";
import {BuildingLabels} from "../../lib/label";

export interface BuildingData {
    building: number;
    families: RichFamily[];
}

export interface BuildingViewProps {
    buildings: BuildingData[];
}

const BuildingView: React.FC<BuildingViewProps> = props => {
    const {buildings} = props;
    const [centerIndex, setIndex] = useState(0);
    const buildingCount = buildings.length;
    const buildingIndices: [string, number][] = [
        ["left", (centerIndex + buildingCount - 1) % buildingCount],
        ["center", centerIndex],
        ["right", (centerIndex + buildingCount + 1) % buildingCount],
    ];
    buildingIndices.sort((a, b) => a[1] - b[1]);

    return (
        <TransitionGroup className="c-building-view">
            {buildingIndices.map(([position, index]) => (
                <CSSTransition timeout={500} key={index} classNames="building-container">
                    <div className={`inner ${position}`} onClick={() => setIndex(index)}>
                        <Building
                            name={BuildingLabels[buildings[index].building]}
                            families={buildings[index].families}
                            current={position === "center"}
                        />
                    </div>
                </CSSTransition>
            ))}
        </TransitionGroup>
    );
};

export default BuildingView;