import React, {useRef} from 'react';
import {BrowserRouter as Router, Switch, Route, NavLink} from "react-router-dom";
import './App.scss';
import {Overview} from "./pages/Overview";
import {Family} from "./pages/Family";
import {RootContainerContext} from "./lib/context";
import {PATH_FAMILY, PATH_OVERVIEW} from "./lib/routes";

const App: React.FC = () => {
  const rootDiv = useRef<HTMLDivElement | null>(null);

  return (
      <Router>
        <div className="app-root" ref={rootDiv}>
          <RootContainerContext.Provider value={() => rootDiv.current || document.body}>
            <nav>
              <h1>
                内蒙古电网包头供电局非侵入式负荷监测可视化平台
              </h1>
              <ul>
                <li>
                  <NavLink to={PATH_OVERVIEW} exact activeClassName="active">总览</NavLink>
                </li>
                <li>
                  <NavLink to={PATH_FAMILY} exact activeClassName="active">按户管理</NavLink>
                </li>
              </ul>
            </nav>
            <Switch>
              <Route path={PATH_OVERVIEW} exact>
                <Overview/>
              </Route>
              <Route path={PATH_FAMILY} exact>
                <Family/>
              </Route>
            </Switch>
          </RootContainerContext.Provider>
        </div>
      </Router>
  );
};

export default App;
