import React, {useCallback, useEffect, useState} from "react";
import moment from "moment";
import "./index.scss";
import RoomView from "./RoomView";
import {AlarmInfo, DeviceAuthType, DeviceType, Indexed, PageAlarmInfo} from "../../api/types";
import DeviceAuth from "./DeviceAuth";
import {useAsync, useAsyncCycle, useQuery, useRefresh} from "../../lib/hooks";
import {OverviewItem, OverviewWithDrawerGraph} from "../../components/OverviewWithDrawerGraph";
import {IconDaily, IconMonthly, IconPower} from "../../components/Icon";
import Block from "../../components/Block";
import EventList from "./EventList";
import {DateSelect, MonthSelect} from "../../components/DateSelect";
import ReactEcharts from "echarts-for-react";
import {DeviceValues, epDistribute} from "./charts";
import ChartGroup from "./ChartGroup";
import {epDailyPopup, epMonthlyPopup, powerDailyPopup} from "../Overview/charts";
import {
    getDeviceAuth,
    getFamilyDeviceEventDetail, getFamilyDeviceLatestEvent,
    getFamilyDevicePowerDetail,
    getFamilyDevicePowerSum,
    getFamilyPowerInfo,
    setDeviceAuth
} from "../../api";
import {objToNumberMap, resample, roundDecimal, toEntriesSorted} from "../../lib/util";
import FamilySelect, {useFamilySelect} from "./FamilySelect";
import { useHistory } from "react-router-dom";
import {PATH_FAMILY} from "../../lib/routes";

const allDevices: DeviceType[] = [
    DeviceType.FRIDGE,
    DeviceType.WASHER,
    DeviceType.AIR_CONDITIONER,
    DeviceType.RICE_COOKER,
    DeviceType.MICROWAVE,
    DeviceType.ELECTRIC_KETTLE,
];


function convertPowerSummary(values: Indexed<number>): DeviceValues {
    return Array.from(objToNumberMap<DeviceType, number>(values).entries())
        .map(([type, value]) => ({type, value: value / 1000}));
}

const mockDeviceStatus = new Map<DeviceType, boolean>();
allDevices.forEach(d => mockDeviceStatus.set(d, true));

const eventPageSize = 4;

async function getAllDeviceStatus(id: string): Promise<Map<DeviceType, AlarmInfo>> {
    const infoList = await Promise.all(allDevices.map(device => getFamilyDeviceLatestEvent(id, device)));
    const map = new Map<DeviceType, AlarmInfo>();
    for (let info of infoList) {
        map.set(info.deviceType, info);
    }
    return map;
}

export const Family: React.FC<{}> = () => {
    const query = useQuery();
    const history = useHistory();
    const queryId = query.get("id");
    // room
    const [buildings, roomId, setRoomId] = useFamilySelect(queryId || undefined);
    if (roomId && roomId !== queryId) {
        history.push(`${PATH_FAMILY}?id=${roomId}`);
    }

    // overview
    const allPowerInfo = useAsyncCycle(useCallback(async () => {
        if (!roomId) return undefined;
        return getFamilyPowerInfo(roomId);
    },  [roomId]), 300000);

    // device status
    const getStatus = useCallback(async () => {
        if (!roomId) return new Map<DeviceType, AlarmInfo>();
        return getAllDeviceStatus(roomId);
    }, [roomId]);
    const allStatus = useAsyncCycle(getStatus, 10000, new Map<DeviceType, AlarmInfo>());

    // event list
    const [page, setPage] = useState(0);
    const getEvents = useCallback(async () => {
            if (!roomId) return Promise.resolve<PageAlarmInfo>({totalPage: 0, allPowerInfos: []});
            return await getFamilyDeviceEventDetail(roomId, page, eventPageSize)
                .then((events) => {
                    events.allPowerInfos.forEach(e => e.power = e.power / 1000);
                    return events;
                });
        }
    , [page, roomId]);
    const events = useAsyncCycle(getEvents, 5000, {totalPage: 0, allPowerInfos: []});

    // power charts
    const getPower = useCallback(async () => {
        if (!roomId) return Promise.resolve([]);
        const allPower = await Promise.all(
            allDevices.map(device => getFamilyDevicePowerDetail(
                roomId, device,
                moment().subtract(1, "hour").valueOf(),
                moment().valueOf()
            ))
        );
        return allPower.map((result, i) => ({
            type: allDevices[i],
            values: result.map<[number, number]>(item => [item.time, item.power / 1000])
        }));
    }, [roomId]);
    const powerDetailList = useAsyncCycle(getPower,  60000, []);

    // auth
    const refresh = useRefresh();
    const [authMap, setAuthMap] = useState<Map<DeviceType, DeviceAuthType>>(new Map());
    const getAuth = useCallback(async () => {
        if (!roomId) return Promise.resolve(new Map<DeviceType, DeviceAuthType>());
        const result = await getDeviceAuth(roomId);
        return objToNumberMap<DeviceType, DeviceAuthType>(result);
    }, [roomId]);
    useEffect(() => {
        getAuth().then(setAuthMap);
    }, [getAuth]);

    const setAuth = (authList: [DeviceType, DeviceAuthType][]) => {
        if (!roomId) return () => {};
        for (let pair of authList) {
            authMap.set(pair[0], pair[1]);
        }
        refresh();
        Promise.all(authList.map(async pair => {
            await setDeviceAuth(roomId, pair[0], pair[1]);
        }))
            .then(getAuth)
            .then(setAuthMap);
    };

    // power summary
    const [powerDay, setPowerDay] = useState(moment());
    const [powerMonth, setPowerMonth] = useState(moment());

    const getDailyPowerSum = useCallback(async () => {
        if (!roomId) return [];
        return await getFamilyDevicePowerSum(roomId, allDevices,
            moment(powerDay).startOf("day").valueOf(),
            moment(powerDay).endOf("day").valueOf(),
        ).then(convertPowerSummary)
    }, [powerDay, roomId]);

    const getMonthlyPowerSum = useCallback(async () => {
        if (!roomId) return [];
        return await getFamilyDevicePowerSum(roomId, allDevices,
            moment(powerMonth).startOf("month").valueOf(),
            moment(powerMonth).endOf("month").valueOf(),
        ).then(convertPowerSummary)
    }, [powerMonth, roomId]);

    const dailyPowerSum = useAsync(getDailyPowerSum, []);
    const monthlyPowerSum = useAsync(getMonthlyPowerSum, []);

    return (
        <div className="page-family">
            <div className="center">
                <RoomView data={allStatus}/>
                <FamilySelect buildings={buildings} roomId={roomId} setRoomId={setRoomId}/>
                <DeviceAuth
                    authMap={authMap}
                    setAuth={setAuth}
                    style={{
                        position: "absolute",
                        left: "4.4rem",
                        bottom: "0.7rem"
                    }}
                />
            </div>
            <div className="left">
                <OverviewWithDrawerGraph style={{
                    height: "3.6rem",
                    padding: "0.8rem 0.3rem"
                }}>
                    <OverviewItem
                        id="1"
                        icon={<IconPower/>}
                        title="实时总功率"
                        value={allPowerInfo ? roundDecimal(allPowerInfo.totalPower / 1000, 2) : 0}
                        unit="kW"
                        graph={
                            <ReactEcharts
                                style={{height: "100%"}}
                                option={powerDailyPopup(
                                    allPowerInfo ?
                                        resample(toEntriesSorted(objToNumberMap(allPowerInfo.powerMap)), 90)
                                            .map(([k, v]) => [k, v / 1000])
                                        : []
                                )}
                            />
                        }
                    />
                    <OverviewItem
                        id="2"
                        icon={<IconDaily/>}
                        title="日总用电量"
                        value={allPowerInfo ? roundDecimal(allPowerInfo.totalDayEPower / 1000, 2) : 0}
                        unit="kW"
                        graph={
                            <ReactEcharts
                                style={{height: "100%"}}
                                option={epDailyPopup(
                                    allPowerInfo ?
                                        toEntriesSorted(objToNumberMap(allPowerInfo.dayEPower))
                                            .map(([time, v]) => [moment(time).format("H"), v / 1000])
                                        : []
                                )}
                            />
                        }
                    />
                    <OverviewItem
                        id="3"
                        icon={<IconMonthly/>}
                        title="月总用电量"
                        value={allPowerInfo ? roundDecimal(allPowerInfo.totalMonthEPower / 1000, 2) : 0}
                        unit="kW"
                        graph={
                            <ReactEcharts
                                style={{height: "100%"}}
                                option={epMonthlyPopup(
                                    allPowerInfo ?
                                        toEntriesSorted(objToNumberMap(allPowerInfo.monthEPower))
                                            .map(([time, v]) => [moment(time).format("D"), v / 1000])
                                        : []
                                )}
                            />
                        }
                    />
                </OverviewWithDrawerGraph>
                <Block title="电量分解">
                    <DateSelect date={powerDay} onChange={setPowerDay} style={{marginTop: "0.1rem"}}/>
                    <ReactEcharts
                        option={epDistribute(dailyPowerSum)}
                        style={{height: "1.5rem", margin: "0.1rem 0 0.2rem"}}
                        notMerge
                    />
                    <MonthSelect date={powerMonth} onChange={setPowerMonth} style={{marginTop: "0.1rem"}}/>
                    <ReactEcharts
                        option={epDistribute(monthlyPowerSum)}
                        style={{height: "1.5rem", margin: "0.1rem 0 0.2rem"}}
                        notMerge
                    />
                </Block>
            </div>
            <div className="right">
                <Block title="事件与报警">
                    <EventList page={page} setPage={setPage} totalPages={events.totalPage} data={events.allPowerInfos}/>
                </Block>
                <Block title="实时功率">
                    <ChartGroup data={powerDetailList}/>
                </Block>
            </div>
        </div>
    );
};
