import {EChartOption} from "echarts";
import {rem} from "../lib/rem";

export function defaultChartOptions(): EChartOption {
    return {
        textStyle: {
            fontSize: rem(12),
            color: "#ffffff"
        },
        color: ["#FF1A3B", "#C95FF2", "#FF7F00", "#00ADFF", "#61E493", "#9382FF", "#F8E71C"],
        tooltip: {
            show: true,
            trigger: "axis",
            textStyle: {
                fontSize: rem(12)
            }
        }
    }
}

export function seriesLine(): EChartOption.SeriesLine {
    return {
        type: "line",
        lineStyle: {
            width: rem(2)
        },
        smooth: true,
        symbol: "none",
    }
}

export function seriesBar(): EChartOption.SeriesBar {
    return {
        type: "bar",
    }
}

export function seriesPie(): EChartOption.SeriesPie {
    return {
        type: "pie",
        roseType: true,
        label: {
            show: false
        },
        labelLine: {
            show: false
        },
        center: ['25%', '50%'],
    }
}

export function xAxis(): EChartOption.XAxis {
    return {
        type: "value",
        axisLabel: {
            fontSize: rem(12)
        },
        axisLine: {
            lineStyle: {
                color: "#EBECF0"
            }
        },
        splitLine: {
            show: false
        },
    }
}

export function yAxis(): EChartOption.YAxis {
    return {
        type: "value",
        axisLabel: {
            fontSize: rem(12)
        },
        axisLine: {
            show: false,
        },
    }
}

export function chartGrid(
    top: number = 40,
    right: number = 30,
    bottom: number = 15,
    left: number = 40
): EChartOption.Grid {
    return {
        top: rem(top),
        right: rem(right),
        bottom: rem(bottom),
        left: rem(left)
    }
}

export function legend(): EChartOption.Legend {
    return {
        show: true,
        top: rem(5),
        left: "center",
        icon: "circle",
        textStyle: {
            color: "#ffffff"
        }
    }
}