import React from "react";
import moment, {Moment} from "moment";
import "./index.scss";
import {StyledProps} from "../common";

export interface DateSelectProps extends StyledProps {
    date: Moment;
    onChange: (date: Moment) => void;
}

export interface DateRangeSelectProps extends StyledProps {
    start: Moment;
    days: number;
    onChange: (start: Moment, end: Moment) => void;
}

export const DateSelect: React.FC<DateSelectProps> = props => {
    const {date, onChange, style} = props;

    const onClickBefore = () => onChange(
        moment(date).startOf("day")
            .subtract(1, "day")
    );

    const onClickAfter = () => onChange(
        moment(date).startOf("day")
            .add(1, "day")
    );

    return (
        <div className="c-date-select" style={style}>
            <div className="btn-left" onClick={onClickBefore}/>
            <div className="value">
                {date.format("YYYY-MM-DD")}
            </div>
            <div className="btn-right" onClick={onClickAfter}/>
        </div>
    );
};

export const MonthSelect: React.FC<DateSelectProps> = props => {
    const {date, onChange, style} = props;

    const onClickBefore = () => onChange(
        moment(date).startOf("month")
            .subtract(1, "month")
    );

    const onClickAfter = () => onChange(
        moment(date).startOf("month")
            .add(1, "month")
    );

    return (
        <div className="c-date-select" style={style}>
            <div className="btn-left" onClick={onClickBefore}/>
            <div className="value">
                {date.format("YYYY-MM")}
            </div>
            <div className="btn-right" onClick={onClickAfter}/>
        </div>
    );
};

export const DateRangeSelect: React.FC<DateRangeSelectProps> = props => {
    const {start, days, onChange, style} = props;
    const end = moment(start).add(days - 1, "days");

    const onClickBefore = () => onChange(
        moment(start).startOf("day")
            .subtract(1, "days"),
        moment(end).startOf("day")
            .subtract(1, "days"),
    );

    const onClickAfter = () => onChange(
        moment(start).startOf("day")
            .add(1, "days"),
        moment(end).startOf("day")
            .add(1, "days"),
    );

    return (
        <div className="c-date-select" style={style}>
            <div className="btn-left" onClick={onClickBefore}/>
            <div className="value">
                {start.format("YYYY-MM-DD")}~{end.format("YYYY-MM-DD")}
            </div>
            <div className="btn-right" onClick={onClickAfter}/>
        </div>
    );
};