import {EChartOption} from "echarts";
import {DeviceType} from "../../api/types";
import {
    chartGrid,
    defaultChartOptions,
    seriesLine, seriesPie,
    xAxis,
    yAxis
} from "../../components/chart-commons";
import {DeviceLabels} from "../../lib/label";
import {rem} from "../../lib/rem";
import moment from "moment";

export type DeviceValues = {
    type: DeviceType;
    name?: string;
    value: number;
}[];

export function epDistribute(data: DeviceValues): EChartOption {
    return {
        ...defaultChartOptions(),
        series: [{
            ...seriesPie(),
            data: data.map(d => ({
                name: d.name || DeviceLabels[d.type],
                value: d.value,
            }))
        }],
        legend: {
            show: true,
            top: rem(20),
            right: rem(25),
            bottom: rem(20),
            icon: "circle",
            orient: "vertical",
            itemGap: rem(20),
            textStyle: {
                color: "#ffffff"
            },
        },
    }
}

export function powerHourly(data: [number, number][], color: string): EChartOption {
    return {
        ...defaultChartOptions(),
        xAxis: {
            ...xAxis(),
            axisLabel: {
                fontSize: rem(10),
                formatter: (value: number) => {
                    return moment(value).format("HH:mm");
                }
            },
            type: "time",
            min: value => {
                const computed = moment().subtract(1, "hour").valueOf();
                return isFinite(value.min) ?
                    Math.min(value.min, computed) : computed;
            },
            max: value => {
                const computed = moment().valueOf();
                return isFinite(value.max) ?
                    Math.max(value.max, computed) : computed;
            },
        },
        yAxis: {
            ...yAxis(),
            axisLabel: {
                fontSize: rem(10)
            },
        },
        series: [{
            ...seriesLine(),
            data,
            step: true,
            smooth: false,
        }],
        grid: chartGrid(5, 10, 15, 30),
        color: [color]
    }
}
