const measure = document.createElement("div");

measure.style.width = "1rem";
measure.style.height = "1rem";
measure.style.position = "fixed";
measure.style.bottom = "-1rem";
measure.style.right = "-1rem";
measure.style.zIndex = "-1";

document.body.appendChild(measure);

export function rem(value?: number): number {
    return measure.clientWidth * (value ? value / 100 : 1);
}
