import React, {Fragment} from "react";
import {DeviceAuthType, DeviceType} from "../../api/types";
import {DeviceIcons, IconAll} from "../../components/Icon";
import {DeviceLabels} from "../../lib/label";
import {StyledProps} from "../../components/common";
import {Tooltip} from "antd";

export interface AuthorizeProps extends StyledProps {
    authMap: Map<DeviceType, DeviceAuthType>;
    setAuth: (authList: [DeviceType, DeviceAuthType][]) => void;
}

export const authDevices: DeviceType[] = [
    DeviceType.FRIDGE,
    DeviceType.WASHER,
    DeviceType.AIR_CONDITIONER,
    DeviceType.RICE_COOKER,
    DeviceType.MICROWAVE,
    DeviceType.ELECTRIC_KETTLE,
    DeviceType.OTHER,
];

function toggled(auth?: DeviceAuthType): DeviceAuthType {
    if (auth === DeviceAuthType.AUTH) {
        return DeviceAuthType.UN_AUTH;
    } else {
        return DeviceAuthType.AUTH;
    }
}

const DeviceAuth: React.FC<AuthorizeProps> = props => {
    const {style, authMap, setAuth} = props;
    const allAuth = authDevices.every(d => authMap.get(d) === DeviceAuthType.AUTH);
    const toggleAuth = (device: DeviceType) =>
        () => setAuth([[device, toggled(authMap.get(device))]]);
    const toggleAll = () => setAuth(
        authDevices.filter(d => allAuth === (authMap.get(d) === DeviceAuthType.AUTH))
            .map((d): [DeviceType, DeviceAuthType] => [d, allAuth ? DeviceAuthType.UN_AUTH : DeviceAuthType.AUTH])
    );

    return (
        <div className="c-device-auth" style={style}>
            <div className="title">设备授权&emsp;
                <Tooltip title={<Fragment>检测到未授权设备开启时，<br/>将发出告警。</Fragment>}><i/></Tooltip>
            </div>
            <div className="devices">
                <div className={`device ${allAuth ? "checked" : ""}`} onClick={toggleAll}>
                    <IconAll className="icon" style={{cursor: "pointer"}}/>
                    <div className="name">全部</div>
                </div>
                {authDevices.map(device => {
                    const Icon = DeviceIcons[device];
                    const name = DeviceLabels[device];
                    const auth = authMap.get(device) === DeviceAuthType.AUTH;

                    return (
                        <div
                            key={device}
                            className={`device ${auth ? "checked" : ""}`}
                            onClick={toggleAuth(device)}
                        >
                            <Icon className="icon" style={{cursor: "pointer"}}/>
                            <div className="name">{name}</div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default DeviceAuth;