import React from "react";
import {DeviceType} from "../../api/types";
import {DeviceIcons} from "../../components/Icon";
import {DeviceLabels} from "../../lib/label";
import {StyledProps} from "../../components/common";

export const displayPayloads: DeviceType[] = [
    DeviceType.FRIDGE,
    DeviceType.WASHER,
    DeviceType.AIR_CONDITIONER,
    DeviceType.RICE_COOKER,
    DeviceType.MICROWAVE,
    DeviceType.ELECTRIC_KETTLE,
];

export interface PayloadsProps extends StyledProps {
    payloadMap: Map<DeviceType, number>;
}

const Payloads: React.FC<PayloadsProps> = props => {
    const {payloadMap, style} = props;
    return (
        <div className="c-payloads" style={style}>
            {displayPayloads.map(device => {
                const Icon = DeviceIcons[device];
                const name = DeviceLabels[device];
                const value = payloadMap.has(device) ? payloadMap.get(device) : "-";
                return (
                    <div className="device" key={device}>
                        <div className="icon">
                            <Icon/>
                            <div className="name">{name}</div>
                        </div>
                        <div className="value">
                            {value}
                        </div>
                    </div>
                );
            })}
        </div>
    );
};

export default Payloads;