import qs from "qs";

interface RequestOption extends RequestInit {
    query?: object;
}

export class FetchError {
    readonly code: number;
    readonly content: any;

    constructor(code: number, content: any) {
        this.code = code;
        this.content = content;
    }
}

export async function fetch<R = undefined>(input: string, init?: RequestOption): Promise<R> {
    let uri = input;
    if (init && init.query) {
        uri = input + qs.stringify(init.query, {
            addQueryPrefix: !input.includes("?"),
            indices: false,
            serializeDate: d => d.getTime().toString()
        });
    }
    const response = await window.fetch(uri, {
        credentials: "include",
        headers: {
            "Content-Type": "application/json; charset=UTF-8",
            "Accept": "application/json"
        },
        ...init,
    });
    const content = await response.text();
    const res = content.length > 0 ? JSON.parse(content) : undefined;
    if (response.ok) {
        return res;
    } else {
        throw new FetchError(response.status, res);
    }
}
