import React, {useCallback, useState} from "react";
import ReactEcharts from 'echarts-for-react';
import "./index.scss";
import BuildingView from "./BuildingView";
import {OverviewItem, OverviewWithDrawerGraph} from "../../components/OverviewWithDrawerGraph";
import {IconDaily, IconMonthly, IconPower} from "../../components/Icon";
import Block from "../../components/Block";
import {DeviceEPInfo, DevicePowerInfo, DeviceType} from "../../api/types";
import Payloads from "./Payloads";
import moment, {Moment} from "moment";
import {DateRangeSelect, DateSelect} from "../../components/DateSelect";
import {epDailyPopup, epMonthlyPopup, epWeekly, powerDaily, powerDailyPopup, ValueByDevice} from "./charts";
import {getAllDeviceEPInfo, getAllDevicePowerInfo, getAllPowerInfo, getDeviceLoad} from "../../api";
import {useAsync, useAsyncCycle} from "../../lib/hooks";
import {objToNumberMap, resample, roundDecimal, sumMergeSortValueLists, toEntriesSorted} from "../../lib/util";
import {emptyBuildings, getRichFamilies} from "../Family/FamilySelect";

const allDevices: DeviceType[] = [
    DeviceType.FRIDGE,
    DeviceType.WASHER,
    DeviceType.AIR_CONDITIONER,
    DeviceType.RICE_COOKER,
    DeviceType.MICROWAVE,
    DeviceType.ELECTRIC_KETTLE,
];

function convertPowerInfo(data: DevicePowerInfo): ValueByDevice[] {
    const result: ValueByDevice[] = Object.keys(data)
        .map(i => parseInt(i) as DeviceType)
        .filter(i => allDevices.includes(i))
        .map(type => ({
            type,
            values: data[type].map(i => [i.time, i.power / 1000])
        }));
    return result.concat({
        type: DeviceType.ALL,
        values: sumMergeSortValueLists(result.map(v => v.values)),
        name: "总功率"
    });
}

function convertEPInfo(data: DeviceEPInfo, startDay: Moment, endDay: Moment): ValueByDevice[] {
    const emptyDays: [string, number][] = Array.from({
        length: moment(endDay).diff(startDay, "days")
    }, (v, i) => moment(startDay).add(i, "days").format("MM-DD"))
        .map(d => [d, 0]);
    return Object.keys(data)
        .map(i => parseInt(i) as DeviceType)
        .filter(i => allDevices.includes(i))
        .map(type => ({
            type,
            values: sumMergeSortValueLists([
                data[type].map<[string, number]>(i => [
                    moment(i.time).format("MM-DD"), i.epower / 1000
                ]),
                emptyDays
            ])
        }));
}

const demoPayloads = new Map<DeviceType, number>();
allDevices.forEach(d => demoPayloads.set(d, 12));

export const Overview: React.FC<{}> = () => {
    // overview
    const families = useAsync(getRichFamilies, emptyBuildings());
    const allPowerInfo = useAsyncCycle(getAllPowerInfo, 300000);

    // device load
    const deviceLoad = objToNumberMap<DeviceType, number>(useAsyncCycle(getDeviceLoad, 5000,{}));

    // daily power
    const [powerChartDate, setPowerChartDate] = useState(moment());
    const powerStartMillis = moment(powerChartDate).startOf("day").valueOf();
    const powerEndMillis = moment(powerChartDate).endOf("day").valueOf();
    const getDailyPower = useCallback(async () => {
            if (!powerStartMillis || !powerEndMillis) return [];
            return await getAllDevicePowerInfo(powerStartMillis, powerEndMillis, allDevices)
                .then(convertPowerInfo);
        },
        [powerEndMillis, powerStartMillis]);
    const dailyPower = useAsyncCycle(getDailyPower, 60000, []);

    // weekly ep
    const [[epChartStart, epChartEnd], setEPChartDates] =
        useState<[Moment, Moment]>([
            moment(),
            moment().add(6, "days")
        ]);
    const epStartMillis = moment(epChartStart).startOf("day").valueOf();
    const epEndMillis = moment(epChartEnd).endOf("day").valueOf();
    const getWeeklyEP = useCallback(async () => {
            if (!epStartMillis || !epEndMillis) return {};
            return await getAllDeviceEPInfo(epStartMillis, epEndMillis, allDevices);
        },
        [epEndMillis, epStartMillis]);
    const weeklyEP = convertEPInfo(useAsync(getWeeklyEP, {}), epChartStart, epChartEnd);

    return (
        <div className="page-overview">
            <div className="left">
                <OverviewWithDrawerGraph style={{
                    height: "3.6rem",
                    padding: "0.8rem 0.3rem"
                }}>
                    <OverviewItem
                        id="1"
                        icon={<IconPower/>}
                        title="实时总功率"
                        value={allPowerInfo ? roundDecimal(allPowerInfo.totalPower / 1000, 2) : 0}
                        unit="kW"
                        graph={
                            <ReactEcharts
                                style={{height: "100%"}}
                                option={powerDailyPopup(
                                    allPowerInfo ?
                                        resample(toEntriesSorted(objToNumberMap(allPowerInfo.powerMap)), 90)
                                            .map(([k, v]) => [k, v / 1000])
                                        : []
                                )}
                            />
                        }
                    />
                    <OverviewItem
                        id="2"
                        icon={<IconDaily/>}
                        title="日总用电量"
                        value={allPowerInfo ? roundDecimal(allPowerInfo.totalDayEPower / 1000, 2) : 0}
                        unit="kW"
                        graph={
                            <ReactEcharts
                                style={{height: "100%"}}
                                option={epDailyPopup(
                                    allPowerInfo ?
                                        toEntriesSorted(objToNumberMap(allPowerInfo.dayEPower))
                                            .map(([time, v]) => [moment(time).format("H"), v / 1000])
                                        : []
                                )}
                            />
                        }
                    />
                    <OverviewItem
                        id="3"
                        icon={<IconMonthly/>}
                        title="月总用电量"
                        value={allPowerInfo ? roundDecimal(allPowerInfo.totalMonthEPower / 1000, 2) : 0}
                        unit="kW"
                        graph={
                            <ReactEcharts
                                style={{height: "100%"}}
                                option={epMonthlyPopup(
                                    allPowerInfo ?
                                        toEntriesSorted(objToNumberMap(allPowerInfo.monthEPower))
                                            .map(([time, v]) => [moment(time).format("D"), v / 1000])
                                        : []
                                )}
                            />
                        }
                    />
                </OverviewWithDrawerGraph>
                <Block title="实时负荷分析">
                    <Payloads
                        style={{marginLeft: "0.28rem", marginTop: "0.26rem"}}
                        payloadMap={deviceLoad}/>
                </Block>
            </div>
            <div className="center">
                <BuildingView buildings={families}/>
            </div>
            <div className="right">
                <Block
                    title="实时功率"
                    titleRight={<DateSelect date={powerChartDate} onChange={setPowerChartDate}/>}
                >
                    <ReactEcharts
                        option={powerDaily(dailyPower)}
                        style={{height: "3.3rem", padding: ".2rem 0 .5rem"}}
                        notMerge
                    />
                </Block>
                <Block
                    title="电量分解"
                    titleRight={<DateRangeSelect
                        start={epChartStart} days={7}
                        onChange={((start, end) => setEPChartDates([start, end]))}
                    />}
                >
                    <ReactEcharts
                        option={epWeekly(weeklyEP)}
                        style={{height: "3.3rem", padding: ".2rem 0 0"}}
                        notMerge
                    />
                </Block>
            </div>
        </div>
    );
};
