import React, {Fragment} from "react";
import {AlarmInfo, AlarmType, DeviceStatusType} from "../../api/types";
import {AlarmLabels, DeviceLabels, DeviceStatusLabels} from "../../lib/label";
import moment from "moment";
import {roundDecimal} from "../../lib/util";

export interface EventListProps {
    page: number;
    setPage: (page: number) => void;
    totalPages: number;
    data: AlarmInfo[];
}

const EventList: React.FC<EventListProps> = props => {
    const {page, setPage, totalPages, data} = props;
    const pageUp = () => page > 0 && setPage(page - 1);
    const pageDown = () => page < totalPages - 1 && setPage(page + 1);

    return (
        <div className="c-event-list">
            <ul className="content">
                {data.map((info, i) =>
                    <li key={i} className={info.alarmType === AlarmType.EXCEPTION ? "alarm" : ""}>
                        <div className="label">{AlarmLabels[info.alarmType]}</div>
                        <div className="content">
                            检测到{DeviceLabels[info.deviceType]}于<em>{
                                moment(info.time).format("YYYY-MM-DD HH:mm:ss")
                            }</em>{DeviceStatusLabels[info.deviceStatus]}{
                                info.deviceStatus === DeviceStatusType.OPEN &&
                                <Fragment>
                                    ，平均功率<em>{roundDecimal(info.power, 2)}kW</em>
                                </Fragment>
                            }
                        </div>
                    </li>)}
            </ul>
            <div className="pager">
                <span className="btn" onClick={pageUp}>&lt;&ensp;</span>
                {page + 1}&ensp;/&ensp;{totalPages}
                <span className="btn" onClick={pageDown}>&ensp;&gt;</span>
            </div>
        </div>
    );
};

export default EventList;