import {EChartOption} from "echarts";
import {DeviceType} from "../../api/types";
import {
    chartGrid,
    defaultChartOptions,
    legend,
    seriesBar,
    seriesLine,
    xAxis,
    yAxis
} from "../../components/chart-commons";
import {DeviceLabels} from "../../lib/label";

export interface ValueByDevice {
    type: DeviceType;
    name?: string;
    values: [any, number][];
}

export function powerDaily(data: ValueByDevice[]): EChartOption {
    return {
        ...defaultChartOptions(),
        xAxis: {
            ...xAxis(),
            name: "(h)",
            type: "time",
        },
        yAxis: {
            ...yAxis(),
            name: "kW"
        },
        series: data.map(d => ({
            ...seriesLine(),
            step: true,
            smooth: false,
            name: d.name || DeviceLabels[d.type],
            data: d.values,
        })),
        grid: chartGrid(),
        legend: legend(),
    }
}

export function epWeekly(data: ValueByDevice[]): EChartOption {
    return {
        ...defaultChartOptions(),
        xAxis: {
            ...xAxis(),
            type: "category"
        },
        yAxis: {
            ...yAxis(),
            name: "kWh"
        },
        series: data.map(d => ({
            ...seriesBar(),
            stack: "1",
            name: d.name || DeviceLabels[d.type],
            data: d.values
        })),
        grid: chartGrid(),
        legend: legend(),
    }
}

export function powerDailyPopup(data: [any, number][]): EChartOption {
    return {
        ...defaultChartOptions(),
        xAxis: {
            ...xAxis(),
            type: "time",
        },
        yAxis: {
            ...yAxis(),
            name: "kW"
        },
        series: [{
            ...seriesLine(),
            data: data,
            smooth: false,
        }],
        grid: chartGrid(),
        legend: legend(),
        color: ["#F5A623"]
    }
}

export function epDailyPopup(data: [any, number][]): EChartOption {
    return {
        ...defaultChartOptions(),
        xAxis: {
            ...xAxis(),
            type: "category",
            name: "(时)",
        },
        yAxis: {
            ...yAxis(),
            name: "kWh"
        },
        series: [{
            ...seriesBar(),
            data: data,
        }],
        grid: chartGrid(),
        legend: legend(),
        color: ["#F5A623"]
    }
}

export function epMonthlyPopup(data: [any, number][]): EChartOption {
    return {
        ...defaultChartOptions(),
        xAxis: {
            ...xAxis(),
            type: "category",
            name: "(日)",
        },
        yAxis: {
            ...yAxis(),
            name: "kWh"
        },
        series: [{
            ...seriesBar(),
            data: data,
        }],
        grid: chartGrid(),
        legend: legend(),
        color: ["#00ADFF"]
    }
}

