import React, {useContext} from "react";
import moment from "moment";
import i_fridge from "./assets/fridge.png";
import i_washer from "./assets/washer.png";
import i_ac from "./assets/ac.png";
import i_ricecooker from "./assets/ricecooker.png";
import i_microwave from "./assets/microwave.png";
import i_kettle from "./assets/kettle.png";
import highlightJson from "./assets/dev-hl.json";
import {RemImage} from "../../components/Icon";
import Lottie from "../../components/Lottie";
import {DeviceStatusType, DeviceType} from "../../api/types";
import {RootContainerContext} from "../../lib/context";
import {DeviceLabels, DeviceStatusLabels} from "../../lib/label";
import {Tooltip} from "antd";
import "antd/lib/tooltip/style/index.css";

export interface DeviceModelProps {
    type: DeviceType;
    left: number;
    top: number;
    time?: number;
    power: number;
    status: DeviceStatusType;
}

function createDeviceModel(
    img: string,
    width: number,
    height: number,
    highlightLeft: number,
    highlightTop: number,
    highlightZ: number,
    textLeft: number,
    textTop: number,
): React.FC<DeviceModelProps> {
    return props => {
        const {left, top, type, status, power, time} = props;
        const on = status === DeviceStatusType.OPEN;
        const name = DeviceLabels[type];

        return (
            <div
                className="c-device-model"
                style={{
                    left: `${left / 100}rem`,
                    top: `${top / 100}rem`,
                    width: `${width / 100}rem`,
                    height: `${height / 100}rem`,
                }}
            >
                <Tooltip
                    autoAdjustOverflow={false}
                    // getPopupContainer={rootContainer}
                    trigger="click"
                    overlayClassName={"c-device-model-ant-tooltip"}
                    title={
                        <div className="c-device-model-popup">
                            <div className="title">
                                {name}
                            </div>
                            <div className="content">
                                <div>{DeviceStatusLabels[status]}时间：<strong>{time ? moment(time).format("HH:mm:ss") : "--"}</strong></div>
                                <div>功率：{power}W</div>
                            </div>
                        </div>
                    }
                >
                    <RemImage
                        style={{cursor: "pointer"}}
                        width={width}
                        height={height}
                        src={img}
                    />
                </Tooltip>
                <div
                    hidden={!on}
                    className={`highlight ${on ? "on" : ""}`}
                    style={{
                        zIndex: highlightZ,
                        left: `${highlightLeft / 100}rem`,
                        top: `${highlightTop / 100}rem`,
                        width: "1rem",
                        height: "1rem",
                    }}
                >
                    <Lottie data={highlightJson}/>
                </div>
                <div
                    className="name"
                    style={{
                        left: `${textLeft / 100}rem`,
                        top: `${textTop / 100}rem`,
                    }}
                >{name}</div>
            </div>
        )
    };
}

interface IDeviceModels {
    [k: string]: React.FC<DeviceModelProps>;
}

export const DeviceModels: IDeviceModels = {
    [DeviceType.FRIDGE]: createDeviceModel(i_fridge, 139, 185, 25, -5, 0, -10, 150),
    [DeviceType.WASHER]: createDeviceModel(i_washer, 80, 100, -10, -10, 0, 20, 100),
    [DeviceType.AIR_CONDITIONER]: createDeviceModel(i_ac, 36, 127, -35, -30, 0, 40, 60),
    [DeviceType.RICE_COOKER]: createDeviceModel(i_ricecooker, 40, 39, -35, -10, -1, 40, 0),
    [DeviceType.MICROWAVE]: createDeviceModel(i_microwave, 64, 63, -25, 0, -1, 40, 0),
    [DeviceType.ELECTRIC_KETTLE]: createDeviceModel(i_kettle, 39, 42, -25, -5, -1, 50, 20),
};