import {fetch} from "../lib/fetch";
import {AlarmInfo, AllPowerInfo, DeviceAuthType, DeviceType, Indexed, PageAlarmInfo, PowerInfo} from "./types";

export const getFamilyPowerInfo = async (id: string): Promise<AllPowerInfo> =>
    await fetch("/family/all_power_info", {
        query: {id}
    });

export const getFamilyDevicePowerSum = async (id: string, types: DeviceType[], start: number, end: number): Promise<Indexed<number>> =>
    await fetch("/family/device_power_sum", {
        query: { id, start, end, types }
    });

export const getFamilyDevicePowerDetail = async (id: string, type: DeviceType, start: number, end: number): Promise<PowerInfo[]> =>
    await fetch("/family/device_power_detail", {
        query: { id, start, end, type }
    });

export const getFamilyDeviceEventDetail = async (id: string, pagenum: number, pagesize: number): Promise<PageAlarmInfo> =>
    await fetch("/family/device_event_detail", {
        query: { id, pagenum, pagesize }
    });

export const getFamilyDeviceLatestEvent = async (id: string, type: DeviceType): Promise<AlarmInfo> =>
    await fetch("/family/device_event_detail_by_type", {
        query: {id, type}
    });

export const getDeviceAuth = async (id: string): Promise<Indexed<DeviceAuthType>> =>
    await fetch("/family/device_auth_info", {
        query: { id }
    });

export const setDeviceAuth = async (id: string, type: DeviceType, status: DeviceAuthType): Promise<boolean> =>
    await fetch("/family/device_auth", {
        method: "POST",
        query: { id, type, status }
    });
