export function roundDecimal(value: number, precision: number): number;
export function roundDecimal(precision: number): (value: number) => number;
export function roundDecimal(
    valueOrPrecision: number,
    precision?: number
): number | ((value: number) => number) {
    if (arguments.length === 1 || typeof precision !== "number") {
        const p = valueOrPrecision;
        return (value: number): number => roundDecimal(value, p);
    } else {
        const rate = 10 ** precision;
        return Math.round(valueOrPrecision * rate) / rate;
    }
}

export function objToMap<T>(obj: {[k: string]: T}): Map<string, T> {
    const map = new Map<string, T>();
    for (let objKey in obj) {
        if (obj.hasOwnProperty(objKey)) {
            map.set(objKey, obj[objKey]);
        }
    }
    return map;
}

export function objToNumberMap<K extends number, T>(obj: {[k: string]: T}): Map<K, T> {
    const map = new Map<K, T>();
    for (let key of Object.keys(obj)) {
        const num = parseFloat(key);
        if (!isNaN(num)) {
            map.set(num as K, obj[key]);
        }
    }
    return map;
}

export function compare<T>(a: T, b: T, desc = false): number {
    if (desc) {
        return -compare(a, b);
    }
    if (a > b) return 1;
    if (a < b) return -1;
    return 0;
}

export function toEntriesSorted<K, V>(map: Map<K, V>, desc = false): [K, V][] {
    return Array.from(map.entries())
        .sort((p1, p2) => compare(p1[0], p2[0], desc));
}

export function resample<T>(arr: T[], rateDivider: number): T[] {
    return arr.filter((v, i) => i % rateDivider === 0);
}

export function sumMergeSortValueLists<T>(lists: [T, number][][]): [T, number][] {
    const map = new Map<T, number>();
    for (let list of lists) {
        for (let pair of list) {
            const val = map.get(pair[0]) || 0;
            map.set(pair[0], val + pair[1]);
        }
    }
    return Array.from(map.entries()).sort((x, y) => compare(x[0], y[0]));
}
